<template>
  <div class="showList">
    <div style="text-align: left;margin: 10px;">
      <button style="width: 100px;" class="btn btn-warning" @click="doAddEdit()">新增</button>
    </div>
    <div>
      <table class="table table-striped table-bordered table-rwd">
        <thead class="table-dark">
          <tr class="tr-only-hide">
            <th v-for="(item, index) in title" :key="index" scope="col">{{item}}</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in this.$store.state.order" :key="index">
            <td data-th="品項">
              {{item.name}}
            </td>
            <td data-th="價格">
              {{item.price}}
            </td>
            <td data-th="功能">
              <button @click="doAddEdit(item)" class="btn btn-success">修改</button>
              <button @click="doDelete(item)" class="btn btn-danger">刪除</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'showList',
  data () {
    return {
      title: ['品項', '價格', '功能']
    }
  },
  methods: {
    doAddEdit (item = '') {
      if (item === '') {
        // 新增
        console.log(this.$store.state.order.length + 1)
        let addList = {
          'orderId': this.$store.state.order.length + 1,
          'name': '',
          'price': 0,
          'count': 0
        }
        console.log(addList)
        this.$router.push({
          // 即是 /addEditList
          name: 'addEditList',
          params: { dataList: JSON.stringify(addList), type: 'add' }
        })
      } else {
        // 編輯
        this.$router.push({
          // 即是 /addEditList
          name: 'addEditList',
          params: { dataList: JSON.stringify(item), type: 'edit' }
        })
      }
    },
    doDelete (deleteList) {
      let inx = this.$store.state.order.findIndex((value) => { if (value.orderId === deleteList.orderId) { return true } })
      // this.$store.state.order.splice(inx, 1)
      let self = this
      this.$swal({
        text: '你確定要 ' + deleteList.name + ' 刪除嗎？',
        type: 'question',
        showCancelButton: true,
        confirmButtonColor: '#d33',
        cancelButtonColor: '#3085d6',
        confirmButtonText: '確定',
        cancelButtonText: '取消'
      }).then((result) => {
        if (result.isConfirmed) {
          // 刪除 => 移除該筆資料
          this.$store.state.order.splice(inx, 1)
          let delectInx = this.$store.state.order.findIndex((value) => { if (value.orderId === deleteList.orderId) { return true } })
          console.log('delectInx => ', delectInx)
          if (delectInx === -1) {
            self.$swal({
              position: 'top-end',
              icon: 'success',
              title: deleteList.name + ' 刪除成功',
              showConfirmButton: false,
              timer: 1500
            })
          } else {
            self.$swal({
              icon: 'error',
              title: deleteList.name + ' 刪除失敗！！！'
            })
          }
        } else {
          self.$swal({
            icon: 'warning',
            title: '取消刪除！！！'
          })
        }
      })
    }
  }
}
</script>

<!--  Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @media (max-width: 736px) {
  .table-rwd{min-width: 100%;}
  /*針對tr去做隱藏*/
  tr.tr-only-hide {display: none !important;}
  /*讓tr變成區塊主要讓他有個區塊*/
  .table-rwd tr{
    display: block;
    border: 1px solid #ddd;
    margin-top: 5px;
  }
  .table-rwd td {
    text-align: left;
    font-size: 15px;
    overflow: hidden;
    width: 100%;
    display: block;
  }
  .table-rwd td:before {
    /*最重要的就是這串*/
    content: attr(data-th) ' : ';
    /*最重要的就是這串*/
    display: inline-block;
    text-transform: uppercase;
    font-weight: bold;
    margin-right: 10px;
    color: #D20B2A;
  }
  /*當RWD縮小的時候.table-bordered 會有兩條線，所以針對.table-bordered去做修正*/
  .table-rwd.table-bordered td,.table-rwd.table-bordered th,.table-rwd.table-bordered{border:0;}
}
</style>



