import Vue from 'vue'
import Router from 'vue-router'
import loginUI from '@/components/loginUI'
import Order from '@/components/Order'
import showList from '@/components/showList'
import addEditList from '@/components/addEditList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/'
    },
    {
      path: '/',
      name: 'loginUI',
      component: loginUI
    },
    {
      path: '/Order',
      name: 'Order',
      component: Order
    },
    {
      path: '/showList',
      name: 'showList',
      component: showList
    },
    {
      path: '/addEditList',
      name: 'addEditList',
      component: addEditList
    }
  ]
})



