<template>
  <div class="order">
    <table class="table table-striped table-bordered table-rwd" style="table-layout:fixed; word-break: break-all;">
      <thead class="table-dark">
        <tr class="tr-only-hide">
          <th v-for="(item, index) in title" :key="index" scope="col">{{item}}</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in this.order" :key="index">
          <td data-th="品項">
            {{item.name}}
          </td>
          <td data-th="價格">
            {{item.price}}
          </td>
          <td data-th="數量">
            <button class="btn btn-warning" @click="doMinus(item.orderId)">-</button>
            <input type="text" style="width: 80px;" v-model="item.count"/>
            <button class="btn btn-secondary" @click="doPlus(item.orderId)">+</button>
          </td>
          <td data-th="總額">
            {{ item.price * item.count}}
          </td>
          <td data-th="移除飲品">
            <button class="btn btn-danger" @click="doDelete(item.orderId)">刪除</button>
          </td>
        </tr>
      </tbody>
      <tfoot style="font-size: 50px;">
        <div>杯數:{{ countTotal.count }}杯</div>
        <div>total:${{ countTotal.total }}</div>
      </tfoot>
    </table>
  </div>
</template>

<script>
export default {
  name: 'Order',
  computed: {
    countTotal: function () {
      let total = 0
      let count = 0
      this.order.forEach((value, index) => {
        total += (value.price * value.count)
        count += value.count
      })
      return {'total': total, 'count': count}
    }
  },
  data () {
    return {
      order: [],
      title: ['品項', '價格', '數量', '總額', '移除飲品']
    }
  },
  methods: {
    doMinus (id) {
      let inx = this.order.findIndex((value) => { if (value.orderId === id) { return true } })
      this.order[inx].count = Number(this.order[inx].count) - 1
      if (this.order[inx].count < 0) {
        this.order[inx].count = 0
      }
    },
    doPlus (id) {
      let inx = this.order.findIndex((value) => { if (value.orderId === id) { return true } })
      this.order[inx].count = Number(this.order[inx].count) + 1
    },
    doDelete (id) {
      let inx = this.order.findIndex((value) => { if (value.orderId === id) { return true } })
      // console.log('index => ', inx)
      // this.order.filter((value, index, arrSelf) => value.indexOf(orderId))
      // this.order.splice(inx, 1)
      this.order[inx].count = 0
    }
  },
  mounted () {
    this.order = []
    if (this.$store.state.order.length > 0) {
      // console.log('store', this.$store.state.order)
      // this.order = Object.assign([], this.$store.state.order)
      this.order = JSON.parse(JSON.stringify(this.$store.state.order))
      // this.order[6] = {
      //   'name': '白玉歐蕾',
      //   'price': 65,
      //   'count': 0
      // }
      console.log('order', this.order)
    }
  }
}
</script>

<!--  Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@media (max-width: 736px) {
  .table-rwd{min-width: 100%;}
  /*針對tr去做隱藏*/
  tr.tr-only-hide {display: none !important;}
  /*讓tr變成區塊主要讓他有個區塊*/
  .table-rwd tr{
    display: block;
    border: 1px solid #ddd;
    margin-top: 5px;
  }
  .table-rwd td {
    text-align: left;
    font-size: 15px;
    overflow: hidden;
    width: 100%;
    display: block;
  }
  .table-rwd td:before {
    /*最重要的就是這串*/
    content: attr(data-th) ' : ';
    /*最重要的就是這串*/
    display: inline-block;
    text-transform: uppercase;
    font-weight: bold;
    margin-right: 10px;
    color: #D20B2A;
  }
  /*當RWD縮小的時候.table-bordered 會有兩條線，所以針對.table-bordered去做修正*/
  .table-rwd.table-bordered td,.table-rwd.table-bordered th,.table-rwd.table-bordered{border:0;}
}
</style>



