<template>
  <div class="topnav">
    <div class="left_nav">
      <div class="main_title">飲料小舖</div>
    </div>

    <div class="right_nav">
      <div v-if="this.$store.state.userLv"></div>
      <div>{{ nav_date }}</div>
      <div>{{ nav_week }}</div>
      <div style="color: #FFFFFF">{{ nav_time }}</div>
      <div class="user_nav" v-if="this.$store.state.userLv">
        <span><button class="btn btn-danger" type="button" @click="btn_logout">登出</button></span>
        <span>{{this.$store.state.userLv}}</span>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
  export default {
    name: "header.vue",
    data() {
      return {
        week_arr: ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'],
        nav_date: '',
        nav_week: '',
        nav_time: '',
        show_home: false,
      }
    },
    methods: {
      getNowDateTime() {
        const now = new Date()
        this.nav_date = (now.getMonth() + 1) + '月' + now.getDate() + '日'
        this.nav_week = this.week_arr[now.getDay()]
        this.nav_time = ((now.getHours() < 12) ? '上午' : '下午') + now.getHours() + ':' + (now.getMinutes() < 10 ? '0' : '') + now.getMinutes()
      },
      btn_logout() {
        let self = this
        this.$swal({
          text: '你確定要登出嗎？',
          type: 'question',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: '確定',
          cancelButtonText: '取消',
          }).then((result) => {
            if (result.isConfirmed) {
              // 登出 => 清空資料
              self.$store.state.userLv = ''
              self.$swal({
                position: 'top-end',
                icon: 'success',
                title: '登出成功',
                showConfirmButton: false,
                timer: 1500
              })
              self.$router.push('/')
            }else {
              self.$swal({
                icon: 'warning',
                title: '取消登出'
              })
            }            
          })
      }
    },
    mounted() {
      setInterval(this.getNowDateTime, 500)
    },
    created() {
      this.getNowDateTime()

    }
  }
</script>

