<template>
  <div class="loginUI">
    <main class="">
      <div class="login" v-if="this.$store.state.userLv === ''">
        <h1>登入畫面</h1>
        <div>
          <div>
            <label for="floatingInput" class="form-label">帳號</label>
            <input
              style="width: 50%;margin: auto;"
              type="text"
              class="form-control"
              id="floatingInput"
              placeholder="請輸入帳號"
              v-model="account"
            />
          </div>
          <div>
            <label for="floatingPassword" class="form-label">密碼</label>
            <input
              style="width: 50%;margin: auto;"
              type="password"
              class="form-control"
              id="floatingPassword"
              placeholder="請輸入密碼"
              v-model="password"
            />
          </div>
          <button style="margin-top: 20px;" class="w-50 btn btn-lg btn-primary" @click="btn_login">
            登入
          </button>
        </div>
      </div>
      <div v-else style="color: red;">
        <div style="position: absolute; top:50%; left: 50%; transform: translate(-50%, -50%);">
          <h1 style="font-weight: bold;">請先按右上登出按鈕<br>再重新登入！！！</h1>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  name: 'loginUI',
  data () {
    return {
      account: '',
      password: ''
    }
  },
  methods: {
    btn_login () {
      if (this.account !== '' && this.password !== '') {
        let checkUser = this.$store.state.user.filter((value, index, arrSelf) => value.Account === this.account)
        if (checkUser.length > 0) {
          if (checkUser[0].Password === this.password) {
            if (checkUser[0].Lv === '管理員') {
              this.$swal({
                position: 'top-end',
                icon: 'success',
                title: '管理員登入',
                showConfirmButton: false,
                timer: 1500
              })
              this.$router.push('/showList')
            } else {
              this.$swal({
                position: 'top-end',
                icon: 'success',
                title: '登入成功',
                showConfirmButton: false,
                timer: 1500
              })
              this.$router.push('/Order')
            }
            this.$store.state.userLv = checkUser[0].Lv
            // 清空 loginUI 的資料
            this.account = ''
            this.password = ''
          } else {
            this.$swal.fire({
              icon: 'error',
              title: '登入失敗\n 密碼錯誤！！！'
            })
          }
        } else {
          this.$swal.fire({
            icon: 'error',
            title: '登入失敗\n 帳號或密碼錯誤！！！'
          })
        }
        console.log(checkUser)
      } else {
        this.$swal.fire({
          icon: 'error',
          title: '登入失敗\n 帳號或密碼，未輸入！！！'
        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .login{
    margin-top: 50px;
  }
  .form-label {
    margin-bottom: 0.5rem;
    font-size: 35px;
  }
</style>



