<template>
    <div class="addEditList">
      <main class="">
        <div class="product">
          <h1>新增 / 編輯 畫面</h1>
          <div>
            <div>
              <label for="floatingName" class="form-label" >品項</label>
              <span v-if="type === 'add'">
                <input
                  style="width: 50%;margin: auto;"
                  type="text"
                  class="form-control"
                  id="floatingName"
                  placeholder="請輸入品項"
                  v-model="itemList.name"
                />
              </span>
              <span v-else-if="type === 'edit'" style="width: 50%; margin: auto; display: block; font-size: 50px;">
                {{itemList.name}}
              </span>
            </div>
            <div>
              <label for="floatingPrice" class="form-label">價格</label>
              <input
                style="width: 50%;margin: auto;"
                type="number"
                class="form-control"
                id="floatingPrice"
                placeholder="請輸入價格"
                v-model="itemList.price"
              />
            </div>
            <div>
              <!-- <p>{{ userData }}</p> -->
              <button style="margin-top: 20px;" class="w-50 btn btn-lg btn-primary"  @click="btn_save">
                储存
              </button>
            </div>
            <div>
              <router-link to="/showList">
                <button style="margin-top: 20px;" class="w-50 btn btn-lg btn-secondary">
                  取消 並 回到首頁
                </button>
              </router-link>
            </div>
          </div>
        </div>
      </main>
    </div>
  </template>
<script>
export default {
  name: 'addEditList',
  data () {
    return {
      itemList: {},
      type: ''
      // name: '',
      // price: ''
    }
  },
  methods: {
    btn_save () {
      if (this.itemList.name === '') {
        this.$swal.fire({
          icon: 'error',
          title: '储存失敗\n 品項，未輸入！！！'
        })
      } else if (this.itemList.price === 0) {
        this.$swal.fire({
          icon: 'error',
          title: '储存失敗\n 價錢，不能為0！！！'
        })
      } else {
        this.itemList.price = Number(this.itemList.price)
        // 新增
        if (this.type === 'add') {
          // console.log('save')
          // console.log('add')
          // console.log('itemList => ', this.itemList)
          // this.$store.state.order[this.$store.state.order.length] = JSON.parse(JSON.stringify(this.itemList))
          this.$store.state.order.push(JSON.parse(JSON.stringify(this.itemList)))
          console.log(this.$store.state.order[this.$store.state.order.length - 1])
          // console.log(Object.values(this.$store.state.order[this.$store.state.order.length - 1]))
          if (Object.values(this.$store.state.order[this.$store.state.order.length - 1]).length === 4) {
            this.$swal({
              position: 'top-end',
              icon: 'success',
              title: '新增成功',
              showConfirmButton: false,
              timer: 1500
            })
            this.$router.push('/showList')
          } else {
            this.$swal.fire({
              icon: 'error',
              title: '新增失敗！！！'
            })
          }
        } else if (this.type === 'edit') {
          let inx = this.$store.state.order.findIndex((value) => { if (value.orderId === this.itemList.orderId) { return true } })
          console.log('save')
          console.log('edit')
          console.log('itemList => ', this.itemList)
          console.log('inx => ', inx)
          this.$store.state.order[inx] = JSON.parse(JSON.stringify(this.itemList))
          console.log(this.$store.state.order[inx])
          if (Object.values(this.$store.state.order[inx]).length === 4) {
            this.$swal({
              position: 'top-end',
              icon: 'success',
              title: '修改成功',
              showConfirmButton: false,
              timer: 1500
            })
            this.$router.push('/showList')
          } else {
            this.$swal.fire({
              icon: 'error',
              title: '修改失敗！！！'
            })
          }
        }
      }
    }
  },
  mounted () {
    let dataList = JSON.parse(this.$route.params.dataList)
    console.log('dataList => ', dataList)
    console.log('typeof => ', typeof dataList)
    console.log('length => ', Object.keys(dataList).length)
    if (Object.keys(dataList).length > 0) {
      this.itemList = dataList
    }
    if (this.$route.params.type) {
      this.type = this.$route.params.type
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .product{
    margin-top: 50px;
  }
  .form-label {
    margin-bottom: 0.5rem;
    font-size: 35px;
  }
</style>



